:root {
    --primary-color: #88ff88;
    --second-color: #88ff8855;
    --bg-primary-color: #88ff8822;
    --text-color: black;
}

* {
    margin: 0;
    color: var(--text-color);
}

*::selection {
    background-color: var(--text-color);
    color: var(--primary-color);
}

body {
    transition: all 0.2s;
}

#header {
    background-color: var(--primary-color);
    position: sticky;
    top: 0;
    padding: 10px;
    display: flex;
    flex-direction: row;
}

#left_piece_header {
    width: 50%;
    margin-left: 0;
}

#right_piece_header {
    width: 50%;
    height: 100%;
    margin-right: 0;
    text-align: right;
}

#root {
    background-color: var(--bg-primary-color);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.light {
    background-color: white;
}

.dark {
    background-color: black;
}

.input_bottom_line {
    background-color: transparent;
    outline: none;
    border: none;
    border-bottom: 3px solid var(--primary-color);
    padding: 10px;
    margin: 8px;
}

*::placeholder {
    color: #909090;
}

.normal_button {
    border: none;
    padding: 10px;
    border-radius: 10px;
    margin: 8px;
    background-color: var(--primary-color);
}

.normal_button:hover {
    opacity: 0.8;
}

.normal_input {
    background-color: var(--second-color);
    padding: 10px;
    border: none;
    border-radius: 10px;
    margin: 8px;
    outline: none;
}

.DRAswitch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
    margin: 2px;
}
  
.DRAswitch .switch_input { 
    opacity: 0;
    width: 0;
    height: 0;
}
  
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--second-color);
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 34px;
}
  
.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 50%;
}
  
.switch_input:checked + .slider {
    background-color: var(--primary-color);
}
  
.switch_input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

.DRAswitch:hover {
    opacity: .8;
}

.disabled {
    opacity: .4;
}

.FAB {
    position: fixed;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    font-size: 20px;
    border: none;
    background-color: var(--primary-color);
}

.FAB:hover {
    opacity: 0.8;
}

.FAB_right {
    right: 8px;
    bottom: 8px;
}

.FAB_left {
    left: 8px;
    bottom: 8px;
}

.DRange {
    appearance: none;
    background-color: var(--second-color);
    border-radius: 10px;
    overflow: hidden;
    margin: 2px;
}

.DRange::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    cursor: pointer;
    width: 15px;
    height: 15px;
    background-color: white;
    border-radius: 50%;
    box-shadow: -407px 0 0 400px var(--primary-color);
    border: 2px solid var(--primary-color);
}

button {
    cursor: pointer;
}

.icon_button {
    border: none;
    background-color: transparent;
    font-size: 20px;
    color: var(--text-color);
}

.bottom_line_button {
    border: none;
    background-color: var(--bg-primary-color);
    border-bottom: 3px solid var(--primary-color);
    padding: 10px;
    margin: 8px;
}

.bottom_line_button:hover {
    background-color: var(--primary-color);
    opacity: .8;
}

.DCard {
    border: 1px solid var(--primary-color);
    border-radius: 30px;
    padding: 6px;
    font-size: 10px;
    background-color: var(--second-color);
    margin: 8px;
    cursor: pointer;
}

.DCard:hover {
    opacity: .8;
}

.DProgress {
    height: 20%;
    width: 20%;
    background-color: var(--second-color);
    border-radius: 10px;
    overflow: hidden;
    margin: 8px;
}

.progress_status {
    width: 0%;
    height: 100%;
    text-align: center;
    background-color: var(--primary-color);
    transition: all 0.2;
    overflow: hidden;
}


.DCLbutton {
    height: 38px;
    width: 38px;
    border-radius: 50%;
    border: 2px solid white;
    background-color: var(--primary-color);
    overflow: hidden;
}

.dra_centerized {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}